@import (less) "color.less";

#secondLine{
 line-height: 1em; 
 padding-bottom: 6px; 
}
.cell-section {
  &.cell__primary {
      font-weight: bold;
  }
  &.cell__uppercase{
    text-transform: uppercase;
  }
  &.cell__font-sm {
      font-size: 14px;
  }
  &.cell__stroke{
    text-decoration: line-through;
  }
  &.cell__text-right{
    text-align:right;
  }
  &.cell__empty{
    height: 20px;
  }
}

.cell-item {
  font-size: 14px;
  &.cell__primary {
      font-weight: bold;
  }
  &.cell__uppercase{
    text-transform: uppercase;
  }
  &.cell__font-sm {
    font-size: 12px;
  }
  &.cell__stroke{
    text-decoration: line-through;
  }
  &.cell__text-right{
    text-align:right;
  }
  &.cell__empty{
    height: 20px;
  }
}

.shopping-cart-header_base{
  padding-top: 10px;
  padding-bottom: 12px;
  padding-left:5px;
  padding-right:5px;
}

.shopping-cart-section {
  color:@dark_white;
  >.shopping-cart-section-header {
    .shopping-cart-header_base;
    padding-left: 10px;
    padding-right: 10px;
    background-color: @dark_cyan;
  }
  >.cj-shopping-cart-section-body {
    background-color: @dirt_gray;
    padding-left: 5px;
    padding-right: 5px;
  }
}

.shopping-cart-item {
  color:@darkest_gray;
  >.shopping-cart-item-header {
    .shopping-cart-header_base;
    background-color: @dirt_gray;
   
  }
  > .shopping-cart-item-header-border{
    //border-top: 1px solid @darkest_white;
    border-bottom: 1px solid @gray;  
  }
  
  >.shopping-cart-item-header:last-child {
    border: 0px none @white !important;
  }
  >.cj-shopping-cart-item-body {
    background-color: @dirt_gray;
    /*bootstrap row col- emulation*/
    margin-left:-5px;
    margin-right:-5px;
    padding-left: 5px;
    padding-right: 5px;
    padding-bottom: 5px;
    border-bottom: 1px solid @gray;
    
  }
}

.cj-shopping-cart-products-container {
  background-color:@white;
  cj-shopping-cart-product ::ng-deep .shopping-cart--product {
     box-sizing: border-box;
     border-bottom: 1px solid @gray;
 }
 &:last-child  cj-shopping-cart-product ::ng-deep .shopping-cart--product {
     box-sizing: border-box;
     border: 0px none transparent !important;
 }
 &:first-child  cj-shopping-cart-product ::ng-deep .shopping-cart--product {
   border-top: 1px solid @light_gray !important;
 }
  &.inside-section{
    margin-left: -5px !important;
    margin-right: -5px !important;
  }
}

.cell-arrow {
  position: absolute;
  top: 30%;
  right: 5px;
  color: @green;
  cursor: pointer;
  &.inverted {
    color: @dark_white;
  }
  &.section-cell-arrow{
    right: 10px;
  }
}
